  font-family: Open-Sans;
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* outline: 1px solid black; */
}

:root {
  --smallInnerPadding: 5px;
  --mediumInnerPadding: 8px;
  --borderRadius: 5px;
  --component-bg-col: hsl(0, 0%, 93%);
}

body {
  font-family: Open-Sans, Arial, Helvetica, sans-serif;
  line-height: 1.5;
  color: hsl(0, 0%, 33%);
}

/* Generic Stylings */
.flex-row-wrapper {
  display: flex;
}

.flex-col-wrapper {
  display: flex;
  flex-direction: column;
}

.hidden {
  display: none !important;
}

/* Header Section */
header {
  height: 65px;
  width: 100%;
  background-color: hsl(0, 0%, 90%);

  padding: var(--mediumInnerPadding) 20px var(--mediumInnerPadding) 20px;

  display: flex;
  align-items: center;
}

input[type="search"] {
  height: 30px;
  min-width: 50ch;

  font-size: 14px;
  font-family: inherit;

  padding-left: var(--smallInnerPadding);
  border-radius: var(--borderRadius);
  border: 1px grey solid;
  /* outline: none; */
}
.error-msg {
  color: rgb(255, 0, 0);
  font-size: 11px;
}

.header-options {
  list-style: none;

  display: flex;
  gap: 4em;

  font-size: 1.4em;
  padding: var(--mediumInnerPadding);
  margin-right: auto;
}

.header-option {
  cursor: pointer;
  user-select: none;
}

.header-option:hover {
  color: hsl(0, 0%, 43%);
}

.header-option:active {
  color: hsl(0, 0%, 30%);
}

section {
  width: 100%;
  height: fit-content;
  padding: 2em 10px 2em 10px;
  margin: 1em 0 1em 0;

  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 2em;

  background-color: hsl(0, 0%, 98%);
}

/* today */
#today {
  justify-content: center;
}

.weather-symbol {
  transform: scale(1.5);
}

.weather-today-info {
  width: auto;
  height: auto;

  line-height: 1.25;

  border-radius: var(--borderRadius);
  background-color: var(--component-bg-col);

  align-items: center;
  gap: 3px;

  padding: 1em 2.5em 1em 2.5em;
}

.weather-other-info {
  width: 100%;

  padding: var(--smallInnerPadding) 12px var(--smallInnerPadding) 3rem;
  border-radius: var(--borderRadius);
  gap: 3px;
}

.weather-today-alerts {
  width: 450px;
  height: 300px;

  border-radius: var(--borderRadius);
  background-color: var(--component-bg-col);

  padding: var(--mediumInnerPadding) 1em 1em 1em;

  line-height: 1.6;
}

.alert-container {
  height: calc(100% - 35px);
  overflow-y: auto;
}

.alert {
  width: 100%;
  padding: var(--smallInnerPadding) var(--mediumInnerPadding)
    var(--smallInnerPadding) var(--mediumInnerPadding);

  background-color: hsl(0, 0%, 80%);
  border-radius: var(--borderRadius);

  margin-bottom: 8px;
  gap: 5px;
}

.alert-headline {
  font-weight: 600;
}

.alert-description {
  font-size: 14px;
}

.no-active-alerts {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
  height: 100%;

  background-color: hsl(0, 0%, 80%);
  border-radius: var(--borderRadius);
}

/*Individual styling*/
.weather-location {
  font-size: 2em;
  font-weight: 500;
}

.weather-condition {
  margin-top: 5px;
  font-weight: 500;
  font-size: 18px;
}

.weather-temp {
  font-size: 3.5em;
  align-self: center;
}

.weather-fl-temp {
  font-size: 1.2em;
  font-weight: 500;
  align-self: center;
}

/**/

/* hourly info */
#hourly-info {
  gap: 0.5em;
  justify-content: center;
  padding: 2em;
  flex-wrap: nowrap;

  overflow-x: auto;
}

.card {
  width: 80px;
  height: 255px;
  flex: 0 0 auto;
  align-items: center;
  gap: 2px;

  line-height: 1.5;
  font-size: 15px;

  background-color: hsl(0, 0%, 93%);

  border-radius: var(--borderRadius);

  padding: var(--mediumInnerPadding) var(--smallInnerPadding)
    var(--mediumInnerPadding) var(--smallInnerPadding);
  transition: all 200ms ease;
}
.card:hover {
  background-color: hsl(0, 0%, 95%);
}

.card-weather-symbol {
  transform: scale(0.95);
}

.card-current-conditions {
  font-size: 13px;
  text-align: center;
  height: 45px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.card-wind-speed {
  margin-top: 8px;
}

.card-rain-chance,
.card-wind-speed,
.card-humidity {
  font-size: 11.3px;
}

.tomorrow-divider {
  width: 20px;
  height: 255px;
  border-radius: var(--borderRadius);
  background-color: hsl(0, 0%, 85%);

  font-size: 14px;

  display: flex;
  align-items: center;
  justify-content: center;

  writing-mode: vertical-rl;
  text-orientation: mixed;
}


/* modal */
.modal {
  z-index: 2;
  border-radius: var(--borderRadius);

  background-color: hsl(0, 0%, 80%);
  color: inherit;
  line-height: 1.5;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);

  padding: 12px;
  align-items: center;

  text-align: center;
}

.modal h2 {
  margin: 0 0 8px 0;
}

.modal-buttons {
  gap: 5px;
}

.modal-buttons button {
  padding: var(--smallInnerPadding) 10px var(--smallInnerPadding) 10px;
  font-size: 0.9em;
  cursor: pointer;

  margin-top: 12px;
}

.overlay {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;

  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(3px);
}
